











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class CommonDatePicker extends Vue {
  private show?: boolean = false;
  private minDate?: Date = new Date();
  private maxDate?: Date = new Date();
  private selectedDate = this.formatDate(new Date);
  private mounted() {
    const dt = new Date();
    dt.setDate(dt.getDate()-50);
    this.minDate = dt;
  }
  private togglePicker() {
    this.show = !this.show;
  }
  private dateChange(value: any) {
    this.togglePicker();
    const dt: string = this.formatDate(value);
    if (dt !== this.selectedDate) {
      this.selectedDate = dt;
      this.emitDate();
    }
  }
  private emitDate() {
    this.$emit('date-change', this.selectedDate);
  }
  private formatDate(date: Date): string {
    const d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    const year = d.getFullYear();

    if (month.length < 2) 
        month = '0' + month;
    if (day.length < 2) 
        day = '0' + day;

    return [year, month, day].join('-');
}
}
